import { Box,Button ,Container, Grid, Typography } from "@material-ui/core";
import React from "react";
import { RenderSectionHeading } from "../common/commonComponent";
import { useStyles } from "./BodyStyles";

import image1 from "../../images/5-2.jpg";
import image2 from "../../images/5-3.jpg";
import image3 from "../../images/5-4.jpg";
import image4 from "../../images/5-6.jpg";
import image5 from "../../images/5-8.jpg";
import image6 from "../../images/5-9.jpg";
import ScrollAnimation from "react-animate-on-scroll";

export default function Portfolio() {
  const classes = useStyles();

  const portfolioData = [
    { url: image1, title: "Stationery Shop Website ", link: "https://himansu46.github.io/Harry-stationary-store-website/" },
    { url: image2, title: "Typing speed Test", link: "https://himansu46.github.io/typing-test/" },
    { url: image3, title: "SignUp/LogIn Page ", link:"https://himansu46.github.io/LogIn-Page/" },
    { url: image4, title: "Simon Game ", link: "https://himansu46.github.io/simon-game/" },
    { url: image5, title: "Quiz Website", link: " https://himansu46.github.io/quiz/" },
    { url: image6, title: "CheckOut Page ", link: "https://himansu46.github.io/checkout/" },
  ];

  return (
    <Box className={classes.sectionDark} id='Portfolio'>
      <ScrollAnimation animateIn='fadeIn'>
        <Grid
          container
          style={{
            displa: "flex",
            justifyContent: "center",
            alignContent: "center",
          }}>
          <Grid item xs={12} sm={8}>
            {RenderSectionHeading({
              smallText: "Portfolio",
              heading: "Let's See My Work",
              alignCenter: true,
            })}
          </Grid>
        </Grid>
        {/* imge section  */}

        <Container maxWidth='xl'>
          <Grid container spacing={2}>
            {portfolioData.map((item, i) => (
              <Grid item xs={6} sm={6} lg={4} key={i}>
                <Box className={classes.imageContainer}>
                  <img
                    src={item.url}
                    alt={item.title}
                    className={classes.responsiveImg}
                  />
                  <Box className={classes.imageOverlay}>
                    <Typography className={classes.overlayTitle}>
                      {item.title}
                    </Typography>
              
                  {/* <a href={item.link}  style={{ textDecoration: 'none' }} target="_blank" rel="noreferrer" ><Button>visit</Button></a> */}
                    <Button variant='contained'><a href={item.link}  style={{ textDecoration: 'none' }} target="_blank" rel="noreferrer" >Visit</a></Button>
                  </Box>
                </Box>
              </Grid>
            ))}
          </Grid>
        </Container>
      </ScrollAnimation>
    </Box>
  );
}
