import { Box, Container, Grid } from "@material-ui/core";
import React from "react";
import { useStyles } from "./BodyStyles";
import image from "../../images/About.jpg";
import { RenderSectionHeading, CardMedia } from "../common/commonComponent";
import AcUnitIcon from "@material-ui/icons/AcUnit";
import PermDeviceInformationIcon from "@material-ui/icons/PermDeviceInformation";

import ToysIcon from "@material-ui/icons/Toys";
import DashboardIcon from "@material-ui/icons/Dashboard";
import ScrollAnimation from "react-animate-on-scroll";


export default function AboutUs() {
  const classes = useStyles();

  const cardMediaData = [
    {
      title: <h3> Front-End Development</h3>,
      description: <div >
                  <ul>
                    <li>HTML(Advanced)</li>
                    <li>CSS(Intermediate)</li>
                    <li>JavaScript(Intermediate)</li>
                    <li>Bootstrap(Intermediate)</li>
                    <li>ReactJs(Intermediate)</li>
                </ul>
                </div>,
      icon: <DashboardIcon />,
    },
    {
      title: <h3> Back-End Development</h3>,
      description: <div >
                  <ul>
                    <li>NodeJs(Intermediate)</li>
                    <li>MongoDB(Intermediate)</li>
                    <li>MySQL(Intermediate)</li>
                    <li>ExpressJs(Intermediate)</li>
                </ul>
                </div>,
      icon: <DashboardIcon />,
    },
     {
      title: <h3>Programming Languages</h3>,
      description: <div>
      <ul>
                    <li>C/C++(Intermediate)</li>
                    <li>Java(Intermediate)</li>
                    <li>Python(Intermediate)</li>
                </ul></div>,
      icon: <PermDeviceInformationIcon />,
    },
    {
      title: <h3>Graphic Designing</h3>,
      description: <div><ul>
                    <li>Adobe Photoshop(Intermediate)</li>
                    <li>Adobe Illustrator(Intermediate)</li>
                </ul></div>,
      icon: <ToysIcon />,
    },
   {
      title: <h3>Content Writing</h3>,
      description: <div><ul>
                    <li>SEO(Intermediate)</li>
                    <li>Microsoft Office(Intermediate)</li>
                    <li>Communication skills(proficient)</li>
                </ul></div>,
      icon: <ToysIcon />,
    },
    {
      title: <h3>Marketing</h3>,
      description: <div><ul>
                    <li>Social Media Marketing(Intermediate)</li>
                    <li>Affiliate Marketer in Bizgurukul</li>
                </ul></div>,
      icon: <AcUnitIcon />,
    },
    
  ];
  return (
    <section className={classes.section} id='About'>
      <ScrollAnimation animateIn='fadeIn'>
        <Container>
          <Grid container spacing={1}>
            <Grid item sm={5}>
              <Box>
                <img
                  src={image}
                  alt=' about me'
                  className={classes.responsiveImg}
                />
              </Box>
            </Grid>
            <Grid item xs={12} sm={7}>
              {RenderSectionHeading({
                smallText: "ABOUT ME",
                heading: "Hello I'm Himansu Sharma",
                description:
                  "A self taught Web Developer and Designer who loves to code simple and amazing websites which most of the people desire to see and have !",
              })}
              <br />
              {/* {CardMedia({
              label: "labae1",
              Desc: "Desc1",
              Icon: <AcUnitIcon />,
            })} */}
              <Grid container>
                {cardMediaData.map((item, i) => (
                  <Grid item xs={12} sm={6} key={i}>
                    {CardMedia({
                      label: item.title,
                      Desc: item.description,
                      Icon: item.icon,
                    })}
                  </Grid>
                ))}
              </Grid>
            </Grid>
          </Grid>
        </Container>
      </ScrollAnimation>
    </section>
  );
}
